.contentmeter {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	height: 5px;
}

.contentmeter__bar {
  height: 100%;
  transform-origin: left center;
	background-color: #F55600;
}

.contentmeter__counter {
	display: inline-block;
	position: absolute;
	top: 5px;
	left: 0;
	padding: 10px;
	text-align: center;
	line-height: 60px;
	font-size: 60px;
	font-weight: bold;
	font-family: sans-serif;
	color: #F55600;
	opacity: .5;
}

.contentmeter__counter:after {
	content: "%";
	font-size: 30px;
}
